/*Add Style*/
/*---------------------------------------------------------------*/
html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 2rem;
}

body, figure, ul, ol, li, dl, dt, dd, p, main, section, article, h1, h2, h3, h4, h5 {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

img {
  vertical-align: bottom;
}

video,
audio {
  vertical-align: bottom;
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: none;
  cursor: pointer;
}

button:focus,
input[type="button"]:focus,
input[type="reset"]:focus,
input[type="submit"]:focus {
  outline: none;
}

div, section, article {
  /*line-height: 1.6;*/
}

textarea {
  vertical-align: bottom;
}

select {
  padding: 0.5em 1em;
}

table {
  border: none;
  border-collapse: collapse;
}
